body {
  background-image: url('../image/cats-map-of-the-world.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  height: 100vh;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}

.breed-select {
  padding: 10px;
  font-size: 16px;
  width: 100%;
  max-width: 300px;
  background-image: url('../image/pngtree-cute-white-cat.jpg');
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: 95% 50%;
  border: none;
  border-radius: 5px;
  padding-right: 30px;
  padding-left: 30px;
  appearance: none;
}

.breed-select option {
  text-align: center;
}

.breed-select select {
  padding-left: 20px;
  padding-right: 0;
}

.loader {
  display: none;
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

.error {
  display: none;
  font-size: 18px;
  font-weight: bold;
  color: #ff0000;
}

.cat-image {
  max-width: 500px;
  height: auto;
  padding: 40px 30px;
}

.cat-info-container {
  margin-left: 20px;
  background-color: white;
  padding: 40px 30px;
}
